import MasterPage from '../components/Master';
import Precio from '../components/Precio';
import fetch from 'isomorphic-unfetch';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';

const Index = props => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del bitcoin</h2>
                <Precio
                    precio={props.precioBitcoin}
                />
            </div>

            <div className="col-md-8">
                <h2 className="mb-3">Noticias sobre bitcoin</h2>
                <Noticias
                    noticias={props.noticias}
                />
            </div>

            <div className="col-md-4">
                <h2>Proximos eventos bitcoin</h2>
                <Eventos
                    eventos={props.eventos}
                />
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
    let url_precio_bitcoin = `https://api.coinmarketcap.com/v2/ticker/1`;
    let url_noticias = `https://newsapi.org/v2/everything?q=bitcoin&from=2018-12-10&sortBy=publishedAt&apiKey=9743aa2a67c34b2db635ed76a68de98b&language=es`;
    let url_eventos = `https://www.eventbriteapi.com/v3/events/search/?token=ZX7CWZJWMPUXIZRYDQAW&locale=es_ES&sort_by=date&location.address=Mexico`;
    
    const precio = await fetch(url_precio_bitcoin);
    const noticias = await fetch(url_noticias);
    const eventos = await fetch(url_eventos);

    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();
    const resEventos = await eventos.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias: resNoticias.articles,
        eventos: resEventos.events
    }
}

export default Index;